@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

body,
h1,
h2,
h3,
h4,
ul {
  margin: 0;
  padding: 0; }

html {
  font-size: 125%; }

li {
  list-style-type: none; }

.wrapper {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto; }

body {
  background-color: #607890;
  background-image: url(data:image/png;base64,);
  color: #10130e;
  text-align: center; }

h1 {
  text-transform: uppercase;
  color: #A9C1D9;
  font-family: "Righteous", cursive;
  font-size: 3rem; }

label,
input {
  display: block;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  margin: 20px 0; }
  label:hover, label:focus,
  input:hover,
  input:focus {
    box-shadow: 0 0 3px 2px #A9C1D9; }

select {
  border-radius: 5px;
  width: 50%;
  margin: 10px 0; }
  select:hover, select:focus {
    box-shadow: 0 0 3px 2px #A9C1D9; }

input[type="number"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
  margin: 0; }

button,
.fa-minus-circle {
  cursor: pointer;
  margin: 10px;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "Righteous", cursive; }

.fa {
  font-family: FontAwesome !important; }

button:hover,
button:focus {
  box-shadow: 0 0 3px 2px #A9C1D9; }

i:hover,
i:focus {
  color: #ff0000; }

.flexParent {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column; }
  .flexParent div
h2,
  .flexParent ul {
    background-color: #F2F0DF;
    border-radius: 5px; }

h2,
h3,
h4 {
  margin: 10px 0;
  font-family: "Righteous", cursive;
  text-transform: uppercase; }

.colorPop {
  color: white; }

.colorPositive {
  color: greenyellow; }

.colorNegative {
  color: red; }

h3 {
  background-color: #F2F0DF;
  border-radius: 5px;
  display: inline-block;
  padding: 5px 10px; }

@media (min-width: 768px) {
  h1 {
    font-size: 4rem; }
  .flexParent {
    flex-direction: row; }
    .flexParent div {
      width: calc(50% - 20px);
      margin-right: 10px; }
  form {
    text-align: left; }
    form button {
      margin: 10px auto;
      display: block; }
  input {
    width: calc(50% - 20px);
    display: inline-block;
    margin-right: 10px; }
  h3 {
    align-self: center; } }

